.container {
  max-width: 1568px;
  width: 100%;
  margin: 0 auto; }
  @media (max-width: 1480px) {
    .container {
      max-width: 1160px; } }

.content {
  padding: 50px 0; }
  @media (max-width: 800px) {
    .content {
      padding: 25px 0; } }

.wrap {
  padding: 0 15px; }

@media (min-width: 1200px) {
  #two-slide .swiper-image,
  #three-slide .swiper-image,
  #five-slide .swiper-image {
    height: 92%; } }

@media (max-width: 1200px) and (min-width: 1480px) {
  #five-slide .swiper-image {
    height: 100%; } }

@media (min-width: 1480px) {
  #four-slide .swiper-image {
    height: 100%; } }

@media (min-width: 1200px) {
  .mid {
    height: max-content;
    position: absolute;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    top: 50%; } }

.policy {
  font-size: 12px;
  font-family: "Proxima Nova Rg";
  font-weight: 400;
  font-style: normal;
  line-height: 24px; }
  .policy a {
    color: #0dbd68;
    font-size: 12px;
    font-weight: bold; }

@-moz-document url-prefix() {
  #file-c,
  #file {
    font-size: 0; }
  .text-file {
    z-index: 1; } }
